@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.binary_container{min-height:70vh;display:flex;flex-direction:column;align-items:center}.binary_container h1{background-image:url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='200px' height='200px' viewBox='0 0 400 400'%3e%3cdefs%3e%3cfilter id='filter' width='400px' height='400px' filterUnits='objectBoundingBox' primitiveUnits='userSpaceOnUse' color-interpolation-filters='sRGB'%3e%3cfeFlood flood-color='%23cd9cf2' flood-opacity='1' x='0' y='0' width='250px' height='250px' result='flood3'%3e%3c/feFlood%3e%3cfeFlood flood-color='%23c2e9fb' flood-opacity='1' x='150px' y='150px' width='250px' height='250px' result='flood4'%3e%3c/feFlood%3e%3cfeGaussianBlur stdDeviation='50 50' x='0%25' y='0%25' width='100%25' height='100%25' in='flood3' edgeMode='none' result='blur1'%3e%3c/feGaussianBlur%3e%3cfeGaussianBlur stdDeviation='50 50' x='0%25' y='0%25' width='100%25' height='100%25' in='flood4' edgeMode='none' result='blur2'%3e%3c/feGaussianBlur%3e%3cfeBlend mode='normal' x='0%25' y='0%25' width='100%25' height='100%25' in='blur2' in2='SourceGraphic' result='blend5'%3e%3c/feBlend%3e%3cfeBlend mode='normal' x='0%25' y='0%25' width='100%25' height='100%25' in='blur1' in2='blend5' result='blend6'%3e%3c/feBlend%3e%3c/filter%3e%3c/defs%3e%3crect width='400' height='400' x='0' y='0' fill='%2366a6ff' filter='url(%23filter)'%3e%3c/rect%3e%3c/svg%3e");background-size:cover;padding:1vh 2vw;border-radius:5em;color:white}.binary_input_container,.binary_output_container{width:50%;display:flex;flex-direction:column;align-items:left;height:10vh;margin:2vh}.binary_input_container input,.binary_output_container input{font-family:inherit;width:100%;border:0;border-bottom:2px solid #9b9b9b;outline:0;font-size:1.3rem;color:#9b9b9b;padding:7px 0;background:transparent;-webkit-transition:border-color 0.2s;transition:border-color 0.2s}.binary_input_container input::-webkit-input-placeholder, .binary_output_container input::-webkit-input-placeholder{color:grey}.binary_input_container input::-moz-placeholder, .binary_output_container input::-moz-placeholder{color:grey}.binary_input_container input:-ms-input-placeholder, .binary_output_container input:-ms-input-placeholder{color:grey}.binary_input_container input::-ms-input-placeholder, .binary_output_container input::-ms-input-placeholder{color:grey}.binary_input_container input::placeholder,.binary_output_container input::placeholder{color:grey}.binary_input_container input:placeholder-shown ~ .form__label,.binary_output_container input:placeholder-shown ~ .form__label{font-size:1.3rem;cursor:text;top:20px}.button_container{margin:20px}.button_container button{font-family:Hack, monospace;background:#0f0f6d;color:#ffffff;cursor:pointer;font-size:2em;padding:1.5rem;border:0;-webkit-transition:all 0.5s;transition:all 0.5s;border-radius:10px;width:auto;position:relative}.button_container button::after{font-weight:400;position:absolute;left:85%;top:31%;right:5%;bottom:0;opacity:0}.button_container button:hover{background:#2b2bff;-webkit-transition:all 0.5s;transition:all 0.5s;border-radius:10px;box-shadow:0px 6px 15px #0000ff61;padding:1.5rem 3.5rem 1.5rem 3.5rem}.button_container button:hover::after{opacity:1;-webkit-transition:all 0.5s;transition:all 0.5s}.button_container button:focus,.button_container button:active{border:none;outline:none}

.previewer_container{border:1px solid black;min-height:80vh;display:flex;justify-content:space-evenly}.previewer_container h1{position:absolute;text-decoration:underline}.previewer_container .code-preview-container{width:40%}.previewer_container .code-preview-container .values{display:flex;flex-direction:column;align-items:left;justify-content:center;height:50%}.previewer_container .code-preview-container .values input,.previewer_container .code-preview-container .values label{width:30%;margin:0 auto}.previewer_container .code-preview-container .valuesToCopy{display:flex;height:50%;justify-content:left;align-items:center}.previewer_container .code-preview-container .valuesToCopy .clipboard{display:flex;flex-direction:column;margin:0 auto;border:1px dashed grey;padding:4vh 4vw;font-size:16px}.previewer_container .form-preview-container{width:40%;display:flex;justify-content:center;align-items:center}.previewer_container .form-preview-container .live-border{border:6px solid #df2c14;padding:4vw 5vw;font-size:16px}

